.euiBody--headerIsFixed {
  /**
   * euiSize = 16px;
   * euiSizeXXL = euiSize * 2.5;
   * euiSizeS = euiSize * 0.5;
   * headerHeightCompensation = euiSizeXXL + euiSizeS + 1;
   */
  padding-top: 49px;
}

.euiBody--headerIsFixed > .euiFlyout,
.euiCollapsibleNav {
  top: 49px;
  height: calc(100% - 49px);
}

.homePageBody {
  padding-top: 20px;
}
